.wrapper {
    width: 100%;
    max-height: min((100vh - 96px) - 60px, 734px);
    box-shadow: rgb(0 0 0 / 12%) 0px 2px 12px;
    background-color: rgba(255, 255, 255, 1);
    padding: 8px 0;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    overflow: hidden auto;
}
